<template>
  <div class="container">
    <div v-if="!isReportOrder">
      <div class="row">
        <div class="col-xs-12">
          <br>
          <h1>Order Details</h1>
        </div>
      </div>
      <hr>
      <router-link :to="{ name: 'merchant', params: { merchantId: 'tgnus' } }">
        <div class="row">
          <div class="col-xs-3">
            <img src="https://via.placeholder.com/220" class="img-fluid" />
          </div>
          <div class="col-xs-9">
            <h3><b>Thirsty Guys</b></h3>
            <h5>Faculty of Science S16 NUS</h5>
          </div>
        </div>
      </router-link>
      <br>
      <div class="row">
        <div class="col-xs-12 text-xs-left">
          <table class="table">
            <tbody>
              <tr>
                <td rowspan="4">
                  <b>Order QR</b>
                  <img src="https://chart.googleapis.com/chart?cht=qr&chs=200x200&chl=BC1637C9-3046-4FB6-83F2-E0029ECD0096&chld=H|1&choe=UTF-8" class="img-fluid" />
                </td>
                <td>
                  <b>Order ID</b>
                </td>
                <td>
                  <b>BC1637C9</b><small> -3046-4FB6-83F2-E0029ECD0096</small>
                </td>
              </tr>
              <tr>
                <td>
                  <b>Q number</b>
                </td>
                <td>
                  <small>Q- </small><b>188</b>
                </td>
              </tr>
              <tr>
                <td>
                  <b>Collection</b>
                </td>
                <td>
                  5 min pick-up
                </td>
              </tr>
              <tr>
                <td>
                  <b>Status</b>
                </td>
                <td>
                  Completed
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <hr>
      <div class="row">
        <div class="col-xs-12">
          <h4>Items</h4>
            <table class="table">
              <thead>
                <tr>
                  <th scope="col">Item</th>
                  <th scope="col">Add on</th>
                  <th scope="col">Total</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, idx) in currentCart.items" :key="idx">
                  <td><b>{{ item.name }}</b></td>
                  <td>
                    + Extra Bubble (SGD 2)<br>
                    + Extra Milk (SGD 2)<br>
                  </td>
                  <td class="text-xs-right">SGD 5</td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td colspan="2" class="text-xs-right">Subtotal</td>
                  <td class="text-xs-right">SGD 20</td>
                </tr>
                <tr>
                  <td colspan="2" class="text-xs-right">Promo description if any</td>
                  <td class="text-xs-right">SGD 20</td>
                </tr>
                <tr>
                  <td colspan="2" class="text-xs-right">Incl. GST</td>
                  <td class="text-xs-right">SGD 20</td>
                </tr>
                <tr class="font-weight-bold">
                  <td colspan="2" class="text-xs-right">Total</td>
                  <td class="text-xs-right">SGD 20</td>
                </tr>
              </tfoot>
            </table>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12">
          <br>
          <button type="button" class="btn btn-outline-danger btn-lg btn-block" @click="reportOrder">Report Order</button>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12 text-xs-center">
          <br>
          <br>
          <!-- <button type="button" class="btn btn-outline-danger btn-lg btn-block" @click="cancelOrder">Cancel Order</button> -->
          <span class="redFont" @click="cancelOrder">Cancel order</span>
        </div>
      </div>
    </div>
    <div v-if="isReportOrder">
      <div class="row">
        <div class="col-xs-12 text-xs-center">
          <br>
          <h1>Order Report</h1>
          <hr>
        </div>
      </div>
    </div>
    <div v-if="isCancelOrder">
      <div class="row">
        <div class="col-xs-12 text-xs-center">
          <br>
          <h1>Order Report</h1>
          <hr>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import { FETCH_ORDER_DETAILS } from "@/store/actions.type";


export default {
  name: "viewOrderDetails",
  data() {
    return {
      isReportOrder: false,
      isCancelOrder: false,
      orderDetails: {
        merchant: {
          name: 'Thirsty Guys',
          address: 'Faculty of Science S16, NUS',
          friendlyId: 'tgnuss16',
          image: 'https://via.placeholder.com/220',
        },
        items: [ 
          {
            name: 'Super Boba',
            additionals: [
              '+XXXXX (SGD 0)',
            ],
            value: '1.2',
          }
        ],
        discount: {
          name: '',
          value: 0,
        },
        tax: 0,
        notes: '',
      },
    };
  },

  computed: {
    ...mapGetters(['currentOrderDetails'])
  },

  methods: {
    cancelOrder () {
      if (window.confirm('Do you want to cancel the order?')) {
        // this.$store.commit(REMOVE_FROM_CART, idx)
        alert("Cancelling your order")
        this.isCancelOrder = !this.isCancelOrder;
      }else{
        return 0;
      }
    },
    
    reportOrder () {
      alert("Reporting your order")
      this.isReportOrder = !this.isReportOrder;
    }
  },

  beforeRouteEnter(to, from, next) {
    this.$store.dispatch(FETCH_ORDER_DETAILS, to.params.orderId);
  }

};
</script>

<style scoped>
.redFont{
  color: red;
  font-size:larger;
}
</style>


